import React, { Fragment, useState, useCallback, useEffect } from "react";
import {
    Navbar,
    Container,
    NavbarToggler,
    Collapse,
    Nav,
    NavItem,
    Form,
    Button,
} from "reactstrap";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { LOGOUT_REQUEST, POSTS_WRITE_REQUEST } from "../redux/types";
import LoginModal from "../components/auth/LoginModal";
import RegisterModal from "../components/auth/RegisterModal";
import SearchInput from "./search/searchInput";

const MenuList = () => {
    const [isOpen, setIsOpen] = useState(false);//   setIsOpen   isOpen  
    const [isOpenBoard, setIsOpenBoard] = useState(false);
    const [isOpenCompetiton, setIsOpenCompetition] = useState(false);
    const [isOpenCompetiton_1, setIsOpenCompetiton_1] = useState(false);
    const [isOpenCompetiton_2, setIsOpenCompetiton_2] = useState(false);
    const [trigger, setTrigger] = useState(1);
    const { isAuthenticated, user, userRole } = useSelector(//store state . , authenticated, user, userRole .
        (state) => state.auth//state.auth   
    );
    //console.log(userRole, "UserRole");

    const dispatch = useDispatch();//  .

    const onLogout = useCallback(() => {
        dispatch({
            type: LOGOUT_REQUEST,
        });
    }, [dispatch]);



    useEffect(() => {
        setIsOpen(false);
        setIsOpenBoard(false);
    }, [user]);//   isopen .


    const handleToggleBoard = () => {
        setIsOpenBoard(!isOpenBoard);
    };//isOpen  .



    const handleToggleCompetiton = () => {
        if (isOpenCompetiton == false)
            setIsOpenCompetition(!isOpenCompetiton);
    };//isOpen  .

    const handleToggleCompetitonClose = () => {
        if (isOpenCompetiton == true)
            setIsOpenCompetition(!isOpenCompetiton);
    };//isOpen  .



    const handleToggleCompetiton_1 = () => {
        console.log(isOpenCompetiton_1)
        setIsOpenCompetiton_1(!isOpenCompetiton_1);
    };//isOpen  .


    const handleToggleCompetiton_2 = () => {
        setIsOpenCompetiton_2(!isOpenCompetiton_2);
    };//isOpen  .


    const Plus_tap = (
        <Fragment>
            <div className="plus_minus">
                +
            </div>
        </Fragment>
    );

    const Minus_tap = (
        <Fragment>
            <div className="plus_minus" >-</div>
        </Fragment>
    );

    return (
        <Fragment>

            <nav className="top_menu_nav">
                <ul className="menu_list">
                    <li> <Link to="/post/60348b1452a7ca64442a5af6" className="top_menu_link">  </Link> </ li>

                    <li onClick={handleToggleBoard}> <div className="top_menu_link"> 
                    {isOpenBoard ? Minus_tap : Plus_tap}</div>
                        <Collapse isOpen={isOpenBoard} >
                            <ul>
                                <li>  <Link to="/post/list/" className="top_menu_link">  </Link> </ li>
                                <li>  <Link to="/post/list/" className="top_menu_link">  </Link> </ li>
                                <li>  <Link to="/post/list/" className="top_menu_link">  </Link> </ li>
                                {isAuthenticated  && (userRole == 'MainJuin' | userRole == 'SubJuin') ?
                                    <li>  <Link to="/post/list/" className="top_menu_link">  </Link> </ li> : <div></div>}
                            </ ul>
                        </Collapse>
                    </ li>
                    <li onClick={handleToggleCompetiton}>
                        <div className="top_menu_link" onClick={handleToggleCompetitonClose}>  {isOpenCompetiton ? Minus_tap : Plus_tap}</div>

                        <Collapse isOpen={isOpenCompetiton}>
                            <ul>
                                <li onClick={handleToggleCompetitonClose}>  <Link to="/post/list/MAZE" className="top_menu_link"> MAZE  </Link> </ li>
                                <li onClick={handleToggleCompetiton_1}>
                                    <div to="/post/list/ " className="top_menu_link">    {isOpenCompetiton_1 ? Minus_tap : Plus_tap}</div>
                                    <ul >
                                        <Collapse isOpen={isOpenCompetiton_1}>
                                            <li onClick={handleToggleCompetitonClose}>  <Link to="/post/list/ " className="top_menu_link">   </Link> </ li>
                                            <li onClick={handleToggleCompetitonClose}>  <Link to="/post/list/DC " className="top_menu_link"> DC  </Link> </ li >
                                            <li onClick={handleToggleCompetitonClose}>  <Link to="/post/list/ " className="top_menu_link">   </Link> </ li >
                                        </Collapse>
                                    </ ul >
                                </ li >
                                <li onClick={handleToggleCompetiton_2}>
                                    <div to="top_menu_link" className="top_menu_link"> sub nav {isOpenCompetiton_2 ? Minus_tap : Plus_tap} </div>
                                    <ul >
                                        <Collapse isOpen={isOpenCompetiton_2}>
                                            <li onClick={handleToggleCompetitonClose}>  <Link to="" className="top_menu_link"> child </Link> </ li>
                                            <li onClick={handleToggleCompetitonClose}>  <Link to="" className="top_menu_link"> child </Link> </ li >
                                            <li onClick={handleToggleCompetitonClose}>  <Link to="" className="top_menu_link"> child </Link> </ li >
                                        </Collapse>
                                    </ ul >
                                </ li >
                            </ ul >
                        </Collapse>
                    </ li >
                    <li>  <Link to="/test" className="top_menu_link">  </Link> </ li >
                    <li>  <Link to="" className="top_menu_link">  </Link> </ li >
                </ ul >

            </nav>

        </Fragment >
    );
};

export default MenuList;