  
import React, { Fragment, useState, useCallback, useEffect } from "react";
import {
  Navbar,
  Container,
  NavbarToggler,
  Collapse,
  Nav,
  NavItem,
  Form,
  Button,
} from "reactstrap";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { LOGOUT_REQUEST, POSTS_WRITE_REQUEST } from "../redux/types";
import LoginModal from "../components/auth/LoginModal";
import RegisterModal from "../components/auth/RegisterModal";
import MenuList from "./MenuList";
import MenuListWindow from "./MenuListWindow";
import SearchInput from "./search/searchInput";
import jQuery from "jquery"


const AppNavbar = () => {
  const [isOpen, setIsOpen] = useState(false);//   setIsOpen   isOpen  
  const { isAuthenticated, user, userRole } = useSelector(//store state . , authenticated, user, userRole .
    (state) => state.auth//state.auth   
  );
  const [isWindowWidth, setWindowWidth] = useState(2);
  //console.log(userRole, "UserRole");

  window.$ = window.jQuery = jQuery;

  const dispatch = useDispatch();//  .

  const onLogout = useCallback(() => {
    dispatch({
      type: LOGOUT_REQUEST,
    });
  }, [dispatch]);

  const handleResize = () => {
    console.log(window.innerWidth)
    if (window.innerWidth > 992) { setWindowWidth(1) }
    else { setWindowWidth(0) }
  }


  useEffect(() => {
    window.addEventListener('resize', handleResize);
    handleResize();
    // console.log(isWindowWidth, " ");
    setIsOpen(false);
  }, [isWindowWidth]);//   isopen .



  const handleToggle = () => {
    setIsOpen(!isOpen);
  };//isOpen  .

  const addPostClick = () => {
    console.log("POSTS_WRITE_REQUEST")
    dispatch({
      type: POSTS_WRITE_REQUEST,
    });
  };


  const authLink = (
    <Fragment>
      <NavItem className="justify-content-center">
        <Form className="col mt-2">
          {user && user.name ? (
            <Link to={`/user/${user.name}/profile`}>
              <Button outline color="light" className="px-3" block>
                <strong>{user ? `${user.name}` : ""}</strong>
              </Button>
            </Link>
          ) : (
            <Button outline color="light" className="px-3" block>
              <strong>No User</strong>
            </Button>
          )}
        </Form>
      </NavItem>
      <NavItem>
        <Form className="col">
          <Link onClick={onLogout} to="#" className="">
            <Button outline color="light" className="mt-2" block>
              Logout
            </Button>
          </Link>
        </Form>
      </NavItem>
    </Fragment>
  );

  const guestLink = (
    <Fragment>
      <NavItem>
        <RegisterModal />
      </NavItem>
      <NavItem>
        <LoginModal />
      </NavItem>
    </Fragment>
  );



  if( window.$(window).scrollTop() == 0) {
    window.$(".AppNavbar_none").css("top", "0px");
    console.log(".");
  }

  console.log(window.innerWidth);

  var lastScrollTop = 0, delta = 15;

  // window.$(".nav_main_link").click(function() {window.location.reload()})

  window.$(window).scroll(function () {
    var scrollTop = window.$(this).scrollTop() /*    ,   (value)      . */
    // Math.abs:    (return).
    if (Math.abs(lastScrollTop - scrollTop) <= delta) //    ~
      return; // ~ 

    if ((scrollTop > lastScrollTop) && (lastScrollTop > 0)) {
      /*    , top      */
      window.$(".AppNavbar_none").css("top", "-100px");
    } else {
      window.$(".AppNavbar_none").css("top", "0px");
    }
    console.log(".");
    lastScrollTop = scrollTop;
  });



  return (
    <Fragment>

      <div className="AppNavbar_none">
        {userRole === "MainJuin" ? (
          <Form className="col mt-2">
            <Link
              to="/post"
              className="btn btn-success block text-white px-3"
              onClick={addPostClick}
            >
              Add Post
            </Link>
          </Form>
        ) : (
          ""
        )}

        <div className="AppNavBar">
          <Navbar color="dark" dark expand="lg" className="sticky-top">
            <div className="nav_body">
              <Link to="/" className="text-white text-decoration-none nav_main_link">
                <h3>M</h3>
              </Link>
              <NavbarToggler onClick={handleToggle} />
              <Collapse className="Collapse_bar" isOpen={isOpen} navbar>
                {/* <SearchInput isOpen={isOpen} /> */}
                {isWindowWidth ? < MenuListWindow /> : < MenuList />}


                <Nav className="top_menu_nav testing" navbar>
                  {isAuthenticated ? authLink : guestLink}
                </Nav>
              </Collapse >
            </div >
          </Navbar >
        </div>
      </div>
    </Fragment >
  );
};

export default AppNavbar;