import React, { useState } from "react";
import { useParams } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
    Form,
    FormGroup,
    Label,
    Input,
    Button,
    Col,
    Progress,
} from "reactstrap";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-editor-classic/src/classiceditor";
import { editorConfiguration } from "../../components/editor/EditorConfig";
import Myinit from "../../components/editor/UploadAdapter";
//import { set } from "mongoose";
import { POST_UPLOADING_REQUEST } from "../../redux/types";
import dotenv from "dotenv";
dotenv.config();

const ListWrite = () => {

    let { categoryName } = useParams();
    const { isAuthenticated, user, userRole } = useSelector((state) => state.auth);  ////store state . , authenticated, user, userRole .
    console.log(user, "  ")
    // if(user.name === undefined) {user.name = ""}
    const [form, setValues] = useState({ title: "", contents: "", fileUrl: "", creatorName: "" });// inputstate  useState
    const dispatch = useDispatch();//reducer dispatch
    const onSubmit = async (e) => {
        await e.preventDefault();
        const { title, contents, fileUrl } = form;
        var creatorName = "";
        if (user == undefined){
            // console.log(form,"form ")
            creatorName = "";
        }
        else {
            creatorName = user.name;
        }
        const category = categoryName
        const token = localStorage.getItem("token");
        const body = { title, contents, fileUrl, category, token, creatorName };
        console.log(body, "")
        dispatch({
            type: POST_UPLOADING_REQUEST,
            payload: body,
        });
    };
    const onChange = (e) => { // input      
        setValues({
            ...form,
            [e.target.name]: e.target.value,//  name value 
        });
    };

    const getDataFromCKEditor = (event, editor) => {//        
        const data = editor.getData();//getdata(ckeditor )       .
        console.log(`${data}  `);

        if (data && data.match("<img src=")) {//  img src    
            const whereImg_start = data.indexOf("<img src=");//   
            console.log(`${whereImg_start}  `);
            let whereImg_end = "";
            let ext_name_find = "";
            let result_Img_Url = "";

            const ext_name = ["jpeg", "png", "jpg", "gif"];//  

            for (let i = 0; i < ext_name.length; i++) {
                if (data.match(ext_name[i])) {// 
                    console.log(data.indexOf(`${ext_name[i]}`), "  ");
                    ext_name_find = ext_name[i];//  
                    whereImg_end = data.indexOf(`${ext_name[i]}`);
                }
            }
            console.log(`  ${ext_name_find}`);
            console.log(whereImg_end, "whereImg_end", " ");

            if (ext_name_find === "jpeg") {
                result_Img_Url = data.substring(whereImg_start + 10, whereImg_end + 4);
            } else {
                result_Img_Url = data.substring(whereImg_start + 10, whereImg_end + 3);
            }
            //   .

            console.log(result_Img_Url, " ");
            setValues({
                ...form,
                fileUrl: result_Img_Url,
                contents: data,
            });//CKeditor   
        } else {
            setValues({
                ...form,
                fileUrl: process.env.REACT_APP_BASIC_IMAGE_URL,
                contents: data,
            });
        }
    };

    console.log(categoryName, "  ")

    return (
        <div>
            {isAuthenticated  && ( userRole == 'MainJuin' | userRole == 'SubJuin' )  ? (
                <Form onSubmit={onSubmit}>
                    <FormGroup className="mb-3">
                        <Label for="title">Title</Label>
                        <Input
                            type="text"
                            name="title"
                            id="title"
                            className="form-control"
                            onChange={onChange}
                        />
                    </FormGroup>
                    {/* CKeditor */}
                    <FormGroup className="mb-3">
                        <Label for="content">Content</Label>
                        <CKEditor
                            editor={ClassicEditor}
                            config={editorConfiguration}
                            onReady={Myinit}
                            onBlur={getDataFromCKEditor}
                        />
                        <Button
                            color="success"
                            block
                            className="mt-3 col-md-2 offset-md-10 mb-3"
                        >
                            
                        </Button>
                    </FormGroup>
                </Form>
            ) : (
                <div className = "only_club">  .</div>
                )}
        </div>
    );
};

export default ListWrite;